import { SignJWT, importPKCS8 } from "jose";
import moment from "moment";

// Use the converted PKCS#8 formatted private key
const PRIVATE_KEY = `-----BEGIN PRIVATE KEY-----
MIGHAgEAMBMGByqGSM49AgEGCCqGSM49AwEHBG0wawIBAQQgKQB67OjkDI7g6Vpy
2Vr7ncmYsIjAzlnsBjG2ogYscSehRANCAARGPEPwzBA+xYswFlZW6P/zTi6Zstos
OpI4UI5t4jHcNqrfC8ilG+OTdZZBhSh4/e+JcQyaoAnntC3C0kgU+UKT
-----END PRIVATE KEY-----`;

const ALGORITHM = "ES256";

// Generate the auth token
export default async function generateAuthToken(telegramId, username) {
  const payload = {
    sub: telegramId.toString(),
    username: username,
    auth: true,
    exp: moment().add(15, "minutes").unix(), // Expiration time in Unix timestamp
  };

  // Import the private key
  const privateKey = await importPKCS8(PRIVATE_KEY, "ES256");

  // Create and sign the JWT
  const jwt = await new SignJWT(payload)
    .setProtectedHeader({ alg: ALGORITHM }) // Set the algorithm
    .setIssuedAt() // Set issued at time
    .setExpirationTime("15m") // Set expiration time (15 minutes)
    .sign(privateKey); // Sign the token with the CryptoKey
  debugger;
  return jwt;
}
