import "./Header.scss";
import { Link } from "react-router-dom";
import logo from "../../img/svgicons/logo.svg";
import profile from "../../img/svgicons/profile.svg";
import { useSelector } from "react-redux";
export default function Header() {
  const userName = useSelector((state) => state.userStats.user.name);
  const userLang = useSelector((state) => state.userStats.user.language);
  return (
    <header className="header">
      <div className="header__row">
        <Link to="/" className="header__logo">
          <img src={logo} alt="logo" />
          <p>Полет в космос</p>
        </Link>
        <Link to="#" className="header__profile">
          <p>{userName || "Иван Иванов"}</p>
          <img src={profile} alt="profile" />
        </Link>
        <div className="header__lang">{userLang}</div>
      </div>
    </header>
  );
}
