import "./Wheel.scss";
import wheel from "../../../img/svgicons/wheel.svg";
import wheelRocket from "../../../img/svgicons/wheel-rocket.svg";

import { useState, useRef } from "react";
import { useOutletContext } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { rocketApi } from "../../../api/api";

import { setGameStats } from "../../../redux/rocket";

import PopupLayout from "../PopupLayout/PopupLayout";

export default function Wheel({}) {
  const wheelTries = useSelector((state) => state.rocket.wheelSpeens);

  const wheelRef = useRef(null);
  const dispatch = useDispatch();
  const context = useOutletContext((context) => context);
  const [triesWheel, setTriesWheel] = useState(wheelTries);
  const [isWheelActive, setIsWheelActive] = useState(false);
  const [adWatched, setAdWatched] = useState(0);
  const [wheelPrize, setWheelPrize] = useState("");
  const [wheelWinners, setWheelWinners] = useState([]);
  const [isPopupOpen, setIsPopupOpen] = useState(false);

  const accessToken = useSelector((state) => state.userStats.tokens.access);
  const wheelPrizes = [
    { text: "100K кликов", countClicks: 100000, weight: 0 },
    { text: "5K кликов", countClicks: 5000, weight: 50 },
    { text: "0.1 TON", countClicks: 0, weight: 0 },
    { text: "10K кликов", countClicks: 10000, weight: 50 },
    { text: "20 TON", countClicks: 0, weight: 0 },
  ];

  const handleClickWheel = () => {
    if (!triesWheel || isWheelActive) {
      return;
    }
    const weightedPrizes = wheelPrizes.reduce((acc, prize, index) => {
      for (let i = 0; i < prize.weight; i++) {
        acc.push(index);
      }
      return acc;
    }, []);
    const randomIndex =
      weightedPrizes[Math.floor(Math.random() * weightedPrizes.length)];
    const randomSection = (360 / wheelPrizes.length) * randomIndex;

    wheelRef.current.style.rotate = `${720 + randomSection}deg`;

    setWheelPrize(wheelPrizes[randomIndex].text);
    setTriesWheel((prev) => --prev);
    setIsWheelActive(true);
    setTimeout(() => {
      wheelRef.current.style.rotate = `0deg`;

      setIsPopupOpen(true);
      setIsWheelActive(false);
      setWheelWinners((prev) => [
        {
          name: context.userName,
          clicks: "0",
          prize: wheelPrizes[randomIndex].text,
        },
        ...prev,
      ]);
      sendGameStats(
        accessToken,
        wheelPrizes[randomIndex].countClicks,
        0,
        "winning",
        new Date().toISOString()
      );
    }, 1400);
  };
  const handleCloseClick = () => {
    setIsPopupOpen(false);
    document.querySelector(".header").classList.remove("popup-open");
  };
  const sendGameStats = async (
    accessToken,
    portionScores,
    totalSessionScore,
    gameMode = "classic",
    lastTapTime
  ) => {
    try {
      const response = await rocketApi.setGameStats({
        accessToken,
        portionScores,
        totalSessionScore,
        gameMode,
        lastTapTime,
      });
      if (!response.status === "success") {
        const errorMessage = response.status || response.message;
        console.error("Ошибка получения токенов:", errorMessage);
        // Здесь можно показать сообщение об ошибке пользователю
        return;
      }
      dispatch(
        setGameStats({
          turboTotal: response.data.daily_turbo_limit_remaining,
          maxClickToday: response.data.daily_tap_limit_remaining,
          rocketCoins: response.data.total_score,
          wheelSpeens: response.data.daily_spin_limit_remaining,
        })
      );
      context.setSessionScores(response.data.total_session_score);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <section className="block-white wheel">
        <h2>Беспроигрышное колесо удачи</h2>
        <p>
          Каждый день в 00:00 по UTC у вас появляется 1 шанс испытать
          беспроигрышное колесо удачи!
        </p>
        <section className="wheel__winners">
          {wheelWinners.map(({ name, clicks, prize }) => {
            return (
              <div className="wheel__winner">
                <div className="wheel__player">
                  <h3>{name}</h3>
                  <p>{clicks}</p>
                </div>
                <p className="wheel__prize">{prize}</p>
              </div>
            );
          })}
        </section>
        <section className="wheel__wheel">
          <img src={wheel} alt="" ref={wheelRef} />
          <img src={wheelRocket} alt="" />
        </section>
        <button className="btn btn-gradient" onClick={handleClickWheel}>
          Играть <span>{triesWheel} попытка</span>
        </button>
        <section className="wheel-ad">
          <h3>Играть за рекламу</h3>
          <div className="wheel-ad__progress">
            <div
              className="progress-bar"
              style={{ width: adWatched / 0.03 + "%" }}
            />
            <div className="count">{adWatched}/3</div>
          </div>
        </section>
        <button className="btn-ad">
          <div className="btn-ad__indicator"></div>
          <div className="btn-ad__text">
            <h3>Реклама</h3>
            <p>Рекалмный текст</p>
          </div>
          <div className="btn-ad__ad">ad</div>
        </button>
      </section>
      {isPopupOpen && (
        <PopupLayout
          isPopupOpen={isPopupOpen}
          handleCloseClick={handleCloseClick}
        >
          <h2 className="popup-title">Поздравляем Вы выиграли:</h2>
          <p className="popup-prize">{wheelPrize}</p>
        </PopupLayout>
      )}
    </>
  );
}
