import { createSlice } from "@reduxjs/toolkit";
const initialState = {
  tokens: {
    access: "",
    refresh: "",
    dateLive: "",
  },
  session: "",
  user: {
    id: null,
    name: "",
    tonWallet: "",
    language: "ru",
    refferralLink: "",
  },
};

export const userSlice = createSlice({
  name: "userStats",
  initialState,
  reducers: {
    setTokens: (state, action) => {
      return (state = {
        ...state,
        tokens: {
          access: action.payload.access,
          refresh: action.payload.refresh,
          dateLive: action.payload.dateLive,
        },
        user: {
          ...state.user,
          id: action.payload.userId,
        },
        session: action.payload.session,
      });
    },
    setUser: (state, action) => {
      return (state = {
        ...state,
        user: {
          ...state.user,
          ...action.payload,
        },
      });
    },
    setSession: (state, action) => {
      state.session = action.payload;
    },
    setRefferalLink: (state, action) => {
      state.user.refferralLink = action.payload;
    },
  },
});

export const { setTokens, setUser, setSession, setRefferalLink } =
  userSlice.actions;

export default userSlice.reducer;
