import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
const initialState = {
  turbo: {
    total: 3,
    max: 3,
  },
  maxClickToday: 1000,
  turboClicks: 20,
  wheelSpeens: 0,
  rocketCoins: {
    total: 32218,
    max: 15000000,
  },
};

export const rocketSlice = createSlice({
  name: "rocket",
  initialState,
  reducers: {
    setTotalRocketCoins: (state, action) => {
      return (state = {
        ...state,
        rocketCoins: {
          ...state.rocketCoins,
          total: state.rocketCoins.total + action.payload,
        },
      });
    },
    setTotalClicks: (state) => {
      --state.maxClickToday;
    },
    setGameStats: (state, action) => {
      return (state = {
        ...state,
        turbo: {
          ...state.turbo,
          total: action.payload.turboTotal,
        },
        maxClickToday: action.payload.maxClickToday,
        wheelSpeens: action.payload.wheelSpeens,
        rocketCoins: {
          ...state.rocketCoins,
          total: action.payload.rocketCoins,
        },
      });
    },
  },
});

export const {
  setTotalRocketCoins,
  setTotalClicks,
  setTotalTurbo,
  setGameStats,
} = rocketSlice.actions;

export default rocketSlice.reducer;
