import "./Rocket.scss"; // Создайте файл RocketAnimation.css

import rocket from "../../../img/svgicons/rocket.svg";
import fire from "../../../img/svgicons/fire/1.svg";

import { useDispatch } from "react-redux";
import { useState, useEffect, useRef } from "react";
import { setTotalRocketCoins, setTotalClicks } from "../../../redux/rocket";

export default function Rocket({
  totalClicksCount,
  isTurbo,
  setPortionScores,
  setSessionScores,
  turboTimeInSecs,
}) {
  const dispatch = useDispatch();
  const containerRef = useRef(null);
  const [stars, setStars] = useState([]);
  const [plusOnes, setPlusOnes] = useState([]);
  const [isAnimatingPlus, setIsAnimatingPlus] = useState(false);

  useEffect(() => {
    const containerHeight = containerRef.current.clientHeight;

    const createStar = () => {
      const x = Math.random() * containerRef.current.clientWidth;
      const size = Math.random() * 2 + 1; // Размер от 1 до 3 пикселей
      return { x, size, y: -size }; // Убираем width и height из объекта звезды
    };

    // Добавляем начальные звезды
    for (let i = 0; i < 2; i++) {
      // Уменьшено количество начальных звезд
      setStars((prevStars) => [...prevStars, createStar()]);
    }

    const animationInterval = setInterval(
      () => {
        setStars((prevStars) => {
          return prevStars.map((star) => {
            const newY = star.y + 1; // Скорость падения
            if (newY > containerHeight) {
              return createStar(); // Создаем новую звезду на верхней границе
            } else {
              return { ...star, y: newY };
            }
          });
        });
      },
      isTurbo ? 5 : 50
    );
    const starSpawnInterval = setInterval(() => {
      stars.length < 40 &&
        setStars((prevStars) => [...prevStars, createStar()]); // Добавляем новую звезду
    }, 1000); // Интервал появления новых звезд (1000 мс = 1 секунда)

    isTurbo && createTapAnim(isTurbo);
    return () => {
      clearInterval(animationInterval);
      clearInterval(starSpawnInterval); // Очищаем интервал появления звезд
    };
  }, [isTurbo]);
  useEffect(() => {
    if (plusOnes.length === 0) return;

    const timer = setTimeout(() => {
      setPlusOnes((prev) => prev.slice(1));
    }, 1000);

    return () => clearTimeout(timer);
  }, [plusOnes]);
  const rocketHandleClick = () => {
    if (!totalClicksCount || isTurbo) {
      return;
    }
    setPortionScores((prev) => ++prev);
    setSessionScores((prev) => ++prev);
    dispatch(setTotalRocketCoins(1));
    dispatch(setTotalClicks());
    createTapAnim(isTurbo);
  };
  const createTapAnim = (isTurbo) => {
    const containerRect = containerRef.current.getBoundingClientRect();
    const minX = 30;
    const maxX = containerRect.width - 30;
    const minY = 30;
    const maxY = containerRect.height - 30;

    const createPlusOne = () => {
      const x = minX + Math.random() * (maxX - minX);
      const y = minY + Math.random() * (maxY - minY);
      return {
        x,
        y,
      };
    };

    if (isTurbo) {
      setIsAnimatingPlus(true); // Начинаем анимацию
      const newPlusOnes = Array.from({ length: 200 }, createPlusOne);
      let index = 0;

      const intervalId = setInterval(() => {
        if (index < newPlusOnes.length) {
          setPlusOnes((prev) => [...prev, newPlusOnes[index]]);
          index++;
        } else {
          clearInterval(intervalId);
          setIsAnimatingPlus(false); // Заканчиваем анимацию
        }
      }, (turboTimeInSecs * 100) / 20); // 2000ms / 20 = 100ms interval (200 iterations / 20 seconds).
    } else {
      const newPlusOne = createPlusOne();
      setPlusOnes((prev) => [...prev, newPlusOne]);
    }
  };

  return (
    <div className="rocket-container" ref={containerRef}>
      <div
        className={`rocket ${isTurbo ? "turbo" : ""}`}
        onClick={rocketHandleClick}
      >
        <div className="rocket-body">
          <img src={rocket} alt="" />
        </div>
        <div className="rocket-fire">
          <img src={fire} alt="" />
        </div>
      </div>
      <div className="stars">
        {stars.map((star, index) => (
          <div
            key={index}
            className="star"
            style={{
              left: `${star.x}px`,
              width: `${star.size}px`,
              height: `${star.size}px`,
              top: `${star.y}px`,
            }}
          ></div>
        ))}
      </div>
      <div className="plus-ones">
        {plusOnes.map((plusOne, i) => (
          <div
            key={i}
            className="plus-one"
            style={{
              left: `${plusOne?.x}px`,
              top: `${plusOne?.y}px`,
            }}
          >
            +1
          </div>
        ))}
      </div>
    </div>
  );
}
