import "./App.scss";
import Header from "./Components/Header/Header";
import Tabs from "./Components/Tabs/Tabs";

import { Outlet, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState, useRef } from "react";

import { userApi } from "./api/api";

import { setUser, setTokens, setSession, setRefferalLink } from "./redux/user";
import { setGameStats } from "./redux/rocket";
import { setTasks } from "./redux/tasks";

import moment from "moment-timezone";
import generateAuthToken from "./commonFn/generate-token";

function App() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const userId = useSelector((state) => state.userStats.user.id);
  const userName = useSelector((state) => state.userStats.user.name);
  const session = useSelector((state) => state.userStats.session);
  const dateString = useSelector((state) => state.userStats.tokens.dateLive);
  const refreshToken = useSelector((state) => state.userStats.tokens.refresh);

  const rocketCoins = useSelector((state) => state.rocket.rocketCoins.total);

  const [sessionScores, setSessionScores] = useState(0);

  const localRefreshToken = JSON.parse(localStorage.getItem("refreshToken"));

  function saveRefreshToken(token, expiresAt) {
    localStorage.setItem(
      "refreshToken",
      JSON.stringify({
        token: token,
        timeLive: expiresAt,
      })
    );
  }
  const getTokens = async (authToken) => {
    try {
      const response = await userApi.getLoginTokens(
        authToken,
        "android",
        "1.2.3"
      );

      if (!response.status === "success") {
        const errorMessage = response.status || response.message;
        console.error("Ошибка получения токенов:", errorMessage);
        return;
      }
      dispatch(
        setTokens({
          access: response.data.access_token,
          refresh: response.data.refresh_token,
          dateLive: response.data.access_token_data.expires_at,
          userId: response.session.user_id,
          session: response.session.session_token,
        })
      );

      saveRefreshToken(
        response.data.refresh_token,
        response.data.refresh_token_data.expires_at
      );
      debugger;
      autorizeByAccessToken(response.data.access_token);
    } catch (error) {
      autorizeByRefreshToken(localRefreshToken);
    }
  };
  const getUser = async (accessToken) => {
    try {
      if (accessToken) {
        const response = await userApi.getUserStats(accessToken);
        if (response.status !== "success") {
          const errorMessage = response.status || response.message;
          console.error("Ошибка получения токенов:", errorMessage);
        }
        dispatch(
          setUser({
            name: response.data.name,
            tonWallet: response.data.ton_wallet_address,
            language: response.data.language.code,
            id: response.data.id,
          })
        );
        dispatch(
          setGameStats({
            turboTotal: response.data.game_status.daily_turbo_limit,
            maxClickToday: response.data.game_status.daily_tap_limit,
            rocketCoins: response.data.game_status.game_currency,
            wheelSpeens: response.data.game_status.daily_spin_limit,
          })
        );
      }
    } catch (error) {}
  };
  const getTasks = async (accessToken) => {
    try {
      if (accessToken) {
        const response = await userApi.getUserTasks(accessToken);
        if (response.status !== "success") {
          const errorMessage = response.status || response.message;
          console.error("Ошибка получения токенов:", errorMessage);
        }
        dispatch(setTasks(response.data.tasks));
      }
    } catch (error) {}
  };
  const getRefferalLink = async (accessToken) => {
    try {
      if (accessToken) {
        const response = await userApi.getUserReferalLink(accessToken);
        if (response.status !== "success") {
          const errorMessage = response.status || response.message;
          console.error("Ошибка получения токенов:", errorMessage);
        }
        dispatch(setRefferalLink(response.data.referral_link));
      }
    } catch (error) {}
  };
  const getSession = async (accessToken) => {
    // try {
    //   const response = await userApi.getSession(accessToken);
    //   if (!response.status === "success") {
    //     const errorMessage = response.status || response.message;
    //     console.error("Ошибка получения токенов:", errorMessage);
    //     return;
    //   }
    //   dispatch(setSession(response.data.session_token));
    // } catch (error) {}
  };
  const getRefreshTokens = async (refreshToken) => {
    try {
      const response = await userApi.getAccessToken(refreshToken);
      if (!response.status === "success") {
        const errorMessage = response.status || response.message;
        console.error("Ошибка получения токенов:", errorMessage);
        // Здесь можно показать сообщение об ошибке пользователю
        return;
      }
      dispatch(
        setTokens({
          access: response.data.access_token,
          refresh: response.data.refresh_token,
          dateLive: response.data.access_token_data.expires_at,
          userId,
        })
      );
      saveRefreshToken(
        response.data.refresh_token,
        response.data.refresh_token_data.expires_at
      );
      autorizeByAccessToken(response.data.access_token);
    } catch (error) {
      console.error("Ошибка при авторизации через refreshToken :", error);
    }
  };

  const autorizeByRefreshToken = async (localRefreshToken) => {
    let daysDifference = 0;

    if (!localRefreshToken) {
      return console.error("Нет localRefreshToken!");
    }
    const currentDate = new Date();
    const expirationDate = new Date(localRefreshToken.timeLive); // Преобразуем строку в объект Date
    const timeDifference = currentDate - expirationDate; // Разница во времени
    daysDifference = timeDifference / (1000 * 60 * 60 * 24); // Конвертация в дни

    if (daysDifference >= 7) {
      alert("Ошибка авторизации!");
      return;
    }
    getRefreshTokens(localRefreshToken.token);
  };
  const autorizeByAccessToken = async (accessToken) => {
    getUser(accessToken);
    getSession(accessToken);
    getRefferalLink(accessToken);
    getTasks(accessToken);
  };
  const isRedirected = useRef(false);
  useEffect(() => {
    const userAgent = navigator.userAgent.toLowerCase();
    const isMobile =
      /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(
        userAgent
      );

    if (!isRedirected.current && !isMobile) {
      isRedirected.current = true;
      navigate("/mobile-only");
      return;
    }

    // const url = new URL(window.location.href);
    // let urlString = url.href.split("#")[0]; // Удаляем фрагмент

    // if (urlString.endsWith("&")) {
    //   urlString = urlString.slice(0, -1); // Удаляем & только если он есть в конце
    // }

    // const newUrl = new URL(urlString);
    // const authToken = newUrl.searchParams.get("auth");

    // ||
    // "eyJhbGciOiJFUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiI4MCIsInNlc3Npb25fdG9rZW4iOiJiOWI3MjA5MC1iYjc1LTRkOTctYjNkNy04MjVlN2ZkYjg2MmUiLCJhdXRoIjp0cnVlLCJleHAiOjE3MzY0MzUyMDN9.or10B9LKA60XLmP_7FSuN_GForKSdERpTOs0brNUCnp3KxMOIeIjTqkcgw251e0iNbD-HsEm0IptEFvSMOsi1g";
  }, []);
  useEffect(() => {
    alert("Нет Telegram.WebApp");
    if (!window.Telegram.WebApp) return;

    const WebApp = window.Telegram.WebApp;
    const { user } = WebApp.initData;

    alert(user);
    if (!user) {
      autorizeByRefreshToken(localRefreshToken);
      return;
    }
    getTokens(generateAuthToken(user.id, user.first_name));
  }, [window.Telegram.WebApp]);
  const hasFifteenMinutesPassed = (dateString) => {
    if (!dateString) {
      return false;
    }

    const date = moment.tz(dateString, "Etc/UTC");
    const now = moment.tz("Etc/UTC");
    const diffInMinutes = now.diff(date, "minutes");
    return diffInMinutes >= -8;
  };

  setInterval(() => {
    if (hasFifteenMinutesPassed(dateString)) {
      getRefreshTokens(refreshToken);
    }
  }, 6000); // 60000 milliseconds = 1 minute
  return (
    <>
      <Header />
      <main className="content">
        <Outlet
          context={{
            userId,
            session,
            sessionScores,
            setSessionScores,
            userName,
            rocketCoins,
          }}
        />
      </main>
      <Tabs />
    </>
  );
}

export default App;

