import "./Index.scss";

import { useState, useEffect } from "react";
import {
  Link,
  useLocation,
  useNavigate,
  useOutletContext,
} from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { rocketApi } from "../../api/api";

import { setTotalRocketCoins, setGameStats } from "../../redux/rocket";

import rocketCoin from "../../img/svgicons/coin.svg";
import rocketTurbo from "../../img/svgicons/turbo.svg";
import friend from "../../img/svgicons/friends.svg";
import wallet from "../../img/svgicons/wallet.svg";
import Rocket from "../Common/Rocket/Rocket";
import BtnInvite from "../Common/BtnInvite/BtnInvite";

export default function Index() {
  let turboScores = 5000;
  let turboTimeInSecs = 20;

  const context = useOutletContext((context) => context);
  const location = useLocation();
  const navigate = useNavigate();
  const { adWatched } = location.state || {};

  const dispatch = useDispatch();
  const totalCoins = useSelector((state) => state.rocket.rocketCoins.total);

  const totalCoinsMax = useSelector((state) => state.rocket.rocketCoins.max);
  const totalTurbo = useSelector((state) => state.rocket.turbo.total);
  const totalTurboMax = useSelector((state) => state.rocket.turbo.max);
  const totalClicksMax = useSelector((state) => state.rocket.maxClickToday);

  const accessToken = useSelector((state) => state.userStats.tokens.access);
  const tonWallet = useSelector((state) => state.userStats.user.tonWallet);

  const [isTurbo, setIsTurbo] = useState(false);
  const [timeLeft, setTimeLeft] = useState(turboTimeInSecs);
  const [portionScores, setPortionScores] = useState(0);
  const [isSendTimerActive, setSendTimerActive] = useState(false);
  const [gameType, setGameType] = useState("classic");
  const sendGameStats = async (
    accessToken,
    portionScores,
    totalSessionScore,
    gameMode = "classic",
    lastTapTime
  ) => {
    try {
      const response = await rocketApi.setGameStats({
        accessToken,
        portionScores,
        totalSessionScore,
        gameMode,
        lastTapTime,
      });
      if (!response.status === "success") {
        const errorMessage = response.status || response.message;
        console.error("Ошибка получения токенов:", errorMessage);
        // Здесь можно показать сообщение об ошибке пользователю
        return;
      }
      dispatch(
        setGameStats({
          turboTotal: response.data.daily_turbo_limit_remaining,
          maxClickToday: response.data.daily_tap_limit_remaining,
          rocketCoins: response.data.total_score,
          wheelSpeens: response.data.daily_spin_limit_remaining,
        })
      );
      context.setSessionScores(response.data.total_session_score);
    } catch (error) {
      console.error(error);
    }
  };

  const turboActivate = () => {
    if (!totalTurbo || isTurbo) {
      return;
    }

    setIsTurbo(true);
    setAnimsNumsValues();
    setPortionScores((prev) => (prev += turboScores));
    context.setSessionScores((prev) => (prev += turboScores));
    setGameType("turbo");
  };
  const setAnimsNumsValues = () => {
    const incrementAmount = turboScores;
    let amount = totalCoins;
    const intervalId = setInterval(() => {
      if (amount < totalCoins + incrementAmount) {
        amount += Math.ceil(incrementAmount / (turboScores * 10));
        dispatch(setTotalRocketCoins(1));
      } else {
        clearInterval(intervalId);
      }
    }, 1);
  };
  useEffect(() => {
    let intervalId;
    if (isTurbo) {
      intervalId = setInterval(() => {
        setTimeLeft((prevTime) => {
          if (prevTime < 1) {
            setIsTurbo(false);
            clearInterval(intervalId);
            return turboTimeInSecs;
          }
          return --prevTime;
        });
      }, 1000); // Обновляем каждую секунду
    }
    return () => clearInterval(intervalId); // Очищаем интервал при размонтировании
  }, [isTurbo]);
  useEffect(() => {
    if (!accessToken || portionScores < 10) return; // проверка условий
    sendGameStats(
      accessToken,
      portionScores,
      context.sessionScores,
      gameType,
      new Date().toISOString()
    );
    setPortionScores(0);
    setGameType("classic");
    setSendTimerActive(false);
  }, [portionScores]);
  useEffect(() => {
    adWatched && turboActivate();
  }, []);

  return (
    <>
      <header className="connect-wallet">
        <p>Дай скорости кораблю и получи TONы</p>
        {tonWallet ? (
          <p>
            Кошелек <span>привязан</span> : {tonWallet.substring(0, 6)}...
          </p>
        ) : (
          <Link to="#" className="btn btn-gradient">
            Подключить кошелек
          </Link>
        )}
      </header>

      <Rocket
        totalClicksCount={totalClicksMax}
        isTurbo={isTurbo}
        setPortionScores={setPortionScores}
        setSessionScores={context.setSessionScores}
        portionScores={portionScores}
        userId={context.userId}
        session={context.session}
        sessionScores={context.sessionScores}
        sendGameStats={sendGameStats}
        turboTimeInSecs={turboTimeInSecs}
        accessToken={accessToken}
      />

      <section className="total-coins">
        <img src={rocketCoin} alt="" />
        <p className="total">{totalCoins}</p>
        <p>|</p>
        <p>{totalCoinsMax}</p>
      </section>
      <label className="text-label">
        <img src={wallet} alt="wallet" />
        <p>Получи до 20.00 TON!</p>
      </label>

      <BtnInvite parentClassName="text-label">
        <img src={friend} alt="friend" />
        <p>Пригласи друзей – Получи Клики!</p>
      </BtnInvite>

      <section className="turbo">
        <div className="count">{totalClicksMax}</div>
        <div className="turbo__count" onClick={() => navigate("/boost")}>
          <img src={rocketTurbo} alt="rocketTurbo" />
          <div className="turbo__text">
            <p>Турбо</p>
            {isTurbo ? (
              <p>{timeLeft}</p>
            ) : (
              <p>
                <span>{totalTurbo}</span> / {totalTurboMax}
              </p>
            )}
          </div>
        </div>
      </section>
    </>
  );
}
